* {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
}

.calculator {
  width: 300px;
  border: 2px solid rgb(190, 185, 188);
  border-radius: 5px;
  box-shadow: 0px 3px 15px rgb(182, 167, 167);
  padding: 15px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.calculator h2 {
  color: wheat;
}

.calculator input {
  width: 100%;
  height: 70px;
  border-radius: 5px;
  padding-right: 6px;
  font-size: 40px;
  font-weight: 500;
  color: rgb(141, 136, 136);
  text-align: right;
  background: transparent;
  box-shadow: 0px 3px 15px rgba(113, 115, 119, 0);
  margin: 15px 0;
}

.calculator button {
  width: 50px;
  height: 50px;
  background: transparent;
  margin: 7px;
  border-radius: 50px;
  box-shadow: 0px 3px 15px rgba(113, 115, 119, 0);
  color: wheat;
  font-size: 20px;
  cursor: pointer;
}

.calculator button:hover {
  transform: scale(1.1);
  color: white;
}

#equal:hover {
  color: wheat;
  transform: scale(1.2);
}